* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

body {
  padding: 0 1rem;
  font-family: "Cormorant SC", serif;
  color: #000;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 800px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  height: auto;
}

header.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
}
header.hero .patinha-group {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 1rem 0 2rem;
}
header.hero .patinha-group .logo {
  position: relative;
  z-index: 1;
  height: 100px;
  width: auto;
}
header.hero .patinha-group .patinha {
  position: absolute;
  width: auto;
  height: 125px;
  opacity: 0.5;
  transform: rotate(15deg);
}
header.hero .text {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.7px;
}
header.hero .text.song {
  margin-bottom: 2rem;
}
header.hero .middle {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
header.hero .middle .parents-row {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
header.hero .middle .parents-row .parent-group {
  display: flex;
  flex-direction: column;
  margin: 1rem 0;
}
header.hero .middle .parents-row .parent-group .parent {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.25rem 0;
  font-size: 14px;
}
header.hero .middle .parents-row .parent-group .parent svg {
  margin-left: 0.5rem;
}
header.hero .image-parent {
  margin: 1rem 0;
}
header.hero .image-parent .image {
  width: 100%;
}
header.hero h2 {
  margin: 0;
  font-size: 24px;
  font-weight: 500;
  margin: 2rem 0;
}
header.hero .countdown {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}
header.hero .countdown .countdown-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 60px;
}
header.hero .countdown .countdown-item .countdown-number {
  font-size: 24px;
  font-weight: 700;
  color: #000;
  line-height: 1;
}
header.hero .countdown .countdown-item .countdown-label {
  font-size: 0.9rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 0.5rem;
}

section {
  padding: 2rem 1rem;
  max-width: 800px;
  margin: 0 auto;
  width: 100%;
}
section h2 {
  border-bottom: 2px solid #000;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
}
section p {
  margin: 0.5rem 0;
}

#local-cerimonia img {
  width: 80%;
  margin: 0 auto;
  display: block;
}
#local-cerimonia .local {
  display: flex;
  justify-content: center;
  width: 100%;
  text-align: center;
  margin-bottom: 2rem;
}
#local-cerimonia iframe {
  width: 100%;
  height: 300px;
  border: none;
  margin-bottom: 0.5rem;
}
#local-cerimonia .aviso {
  display: block;
  margin-top: 0.5rem;
}

#trajes .glide-parent {
  max-width: 60%;
  margin: 0 auto;
}

#presente .presente-button {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 0.75rem;
  padding: 0.75rem 0;
  border-radius: 4px;
  text-decoration: none;
  font-weight: 600;
  background: #000;
  color: #fff;
}
#presente .lembrete {
  margin-top: 0.5rem;
  font-style: italic;
}

#fotos .glide-parent {
  margin: 0 auto;
  max-width: 320px;
}
#fotos .glide-parent .glider-historia .glide__slide {
  display: flex;
  justify-content: center;
}
#fotos .glide-parent .glider-historia .glide__slide img {
  width: 100%;
  height: 445px;
  object-fit: cover;
  margin: 0 auto;
}
#fotos .glide-parent .arrows {
  display: flex;
  justify-content: center;
  align-items: center;
}
#fotos .glide-parent .arrows .arrow {
  width: 50%;
  padding: 0.5rem;
  background: transparent;
  border: 1px solid #000;
  margin-top: 0.5rem;
  cursor: pointer;
}
#fotos .glide-parent .arrows .arrow.left {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
#fotos .glide-parent .arrows .arrow.right {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  border-left: none;
}
#fotos .glide-parent .arrows .arrow svg {
  height: 20px;
}

#mensagens .formulario .form-group {
  margin-bottom: 1.5rem;
}
#mensagens .formulario .form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #333;
}
#mensagens .formulario .form-group input,
#mensagens .formulario .form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #ddd;
  border-radius: 5px;
  font-family: inherit;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}
#mensagens .formulario .form-group input:focus,
#mensagens .formulario .form-group textarea:focus {
  outline: none;
  border-color: #000;
}
#mensagens .formulario .form-group textarea {
  resize: vertical;
  min-height: 100px;
}
#mensagens .formulario .butao {
  background: #000;
  color: white;
  padding: 0.75rem 2rem;
  border: none;
  border-radius: 25px;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
}